import { graphql, useStaticQuery } from 'gatsby'

export default function useSiteMetadata() {
  const data = useStaticQuery(graphql`
    query getMetadata {
      site {
        siteMetadata {
          title
          description
          repoUrl
          infoData {
            contact {
              github_handle
              twitter_handle
              linkedin_tag
              linkedin_name
            }
            description
            background_color
          }
        }
      }
    }
  `)
  return data.site.siteMetadata
}
